import React from 'react'
import PropTypes from 'prop-types'
import NumberFormat from 'react-number-format';
import ConsolidatedCheckComponent from "./is-consolidated-check.component";
import NewDebtCheckComponent from "./is-new-debt-check.component";
import ClientCheckComponent from "./is-client-check.component";

const DebtTableRowComponent = ({ debt, personList, onDelete, updateIsConsolidated, updateIsNewDebt, updateClient }) => (
    <tr>
      <td className="text-left">{debt.debtType}</td>
      <td className="text-right">{debt.lendableEquity}%</td>
      <td className="text-right">
        <NumberFormat value={debt.totalValue}
                      displayType={'text'}
                      thousandSeparator={true}
                      prefix={'$'} />
      </td>
      <td className="text-right">
        <NumberFormat value={debt.amountOwed}
                      displayType={'text'}
                      thousandSeparator={true}
                      prefix={'$'} />
      </td>
      <td className="text-right">
        <NumberFormat value={debt.monthlyPayment}
                      displayType={'text'}
                      thousandSeparator={true}
                      prefix={'$'} />
      </td>
      <td className="text-right">
        {debt.remainingTermMonths}
      </td>
      <td className="text-right">
        {debt.interestRate}%
      </td>
      <td className="text-center">
        <ConsolidatedCheckComponent id={debt.id}
                                    isConsolidated={debt.isConsolidated}
                                    updateIsConsolidated={updateIsConsolidated}/>
      </td>
      <td className="text-center">
        <NewDebtCheckComponent id={debt.id}
                               isNewDebt={debt.isNewDebt}
                               updateIsNewDebt={updateIsNewDebt} />
      </td>
      <td>
        {personList.map(person => (
          <div key={debt.id+'=>'+person.id} className="text-nowrap">
            <ClientCheckComponent debt={debt}
                                  person={person}
                                  updateClient={updateClient}/>
          </div>
        ))}
      </td>
      <td className="text-center">
        <button className="btn btn-sm btn-danger"
                onClick={onDelete}>
          Delete
        </button>
      </td>
    </tr>
)

DebtTableRowComponent.propTypes = {
  debt: PropTypes.shape({
        id: PropTypes.number.isRequired,
        debtType: PropTypes.string.isRequired,
        totalValue:  PropTypes.number.isRequired,
        amountOwed:  PropTypes.number.isRequired,
        interestRate:  PropTypes.number.isRequired,
        monthlyPayment:  PropTypes.number.isRequired,
        remainingTermMonths:  PropTypes.number.isRequired,
        isConsolidated: PropTypes.bool.isRequired,
        isNewDebt: PropTypes.bool.isRequired,
        clients: PropTypes.arrayOf(PropTypes.number.isRequired)
      }).isRequired,
  onDelete: PropTypes.func.isRequired,
  updateIsConsolidated: PropTypes.func.isRequired,
  updateIsNewDebt: PropTypes.func.isRequired,
  updateClient: PropTypes.func.isRequired,
}

export default DebtTableRowComponent