import {DebtEvents} from "./debt.constants";
import {PersonEvents} from "../person/person.constants";

const debtClientReducer = (state = [], action) => {
  switch (action.type) {

    case DebtEvents.UPDATE_CLIENT:
      return action.debtClient.isClient
          ? [...state.filter(c => c !== action.debtClient.personId), action.debtClient.personId]
          : state.filter(c => c !== action.debtClient.personId)

    case PersonEvents.DELETE_PERSON:
      return state.filter(c => c !== action.id)

    default:
      return state
  }
}

export default debtClientReducer