import React from 'react'
import NumberFormat from "react-number-format";

const getTotalAssets = (debtList) =>
    (debtList.reduce((acc, {totalValue}) => acc + totalValue, 0))

const getTotalLiabilities = (debtList) =>
    (debtList.reduce((acc, {amountOwed}) => acc + amountOwed, 0))

const getTotalLendableEquity = (debtList) =>
    (debtList.reduce((acc, debt) => acc + (0.01 * debt.lendableEquity * debt.totalValue), 0))

const getExpectedInterest = (debtList) =>
    (debtList.reduce((acc, debt) => acc + (debt.monthlyPayment * debt.remainingTermMonths) - debt.amountOwed, 0))


const ResultsComponent = ({ personList, debtList }) => (
    <div>
      <div class="row">
        <dl class="col-auto">
          <dt>Total Assets</dt>
          <dd className="text-right mb-3">
            <NumberFormat value={getTotalAssets(debtList)}
                          displayType={'text'}
                          thousandSeparator={true}
                          prefix={'$'} />
          </dd>

          <dt>Total Liabilities</dt>
          <dd className="text-right mb-3">
            <NumberFormat value={getTotalLiabilities(debtList)}
                          displayType={'text'}
                          thousandSeparator={true}
                          prefix={'$'} />
          </dd>

          <dt>Lendable Equity</dt>
          <dd className="text-right mb-3">
            <NumberFormat value={getTotalLendableEquity(debtList)}
                          displayType={'text'}
                          thousandSeparator={true}
                          prefix={'$'} />
          </dd>

          <dt>Expected Intrest</dt>
          <dd className="text-right mb-3">
            <NumberFormat value={getExpectedInterest(debtList)}
                          displayType={'text'}
                          thousandSeparator={true}
                          prefix={'$'} />
          </dd>
        </dl>
      </div>
    </div>
)

export default ResultsComponent