import {addPerson} from "./redux/person/person.actions";
import {addDebt} from "./redux/debt/debt.actions";
import {DebtTypes, getDefaultLendableEquity} from "./redux/debt/debt.constants";

const loadData = (store) => {

  // loading people
  store.dispatch(addPerson({name:'Jack Johnson', annualIncome:88000, isBorrower:true}))
  store.dispatch(addPerson({name:'Jill Johnson', annualIncome:98000, isBorrower:true}))
  store.dispatch(addPerson({name:'Tom Jones', annualIncome:103000, isBorrower:false}))

  // loading debts
  store.dispatch(addDebt({
    debtType: DebtTypes.HOME,
    lendableEquity: getDefaultLendableEquity(DebtTypes.HOME),
    totalValue: 100000,
    amountOwed: 80000,
    monthlyPayment: 400,
    interestRate: 5,
    remainingTermMonths: 300,
    isConsolidated: false,
    isNewDebt: true,
    clients:[0,1]
  }))
  store.dispatch(addDebt({
    debtType: DebtTypes.AUTO,
    lendableEquity: getDefaultLendableEquity(DebtTypes.AUTO),
    totalValue: 35000,
    amountOwed: 10000,
    monthlyPayment: 200,
    interestRate: 6,
    remainingTermMonths: 30,
    isConsolidated: true,
    isNewDebt: false,
    clients:[1]
  }))

}

export default loadData