import { connect } from 'react-redux'
import PersonTable from "./person-table.component";
import {deletePerson, updatePersonBorrower} from "../../redux/person/person.actions";

const mapStateToProps = state => ({
  personList: state.personList
})

const mapDispatchToProps = (dispatch, ownProps) => ({
  deletePerson: id => dispatch(deletePerson(id)),
  updatePersonBorrower: personBorrower => dispatch(updatePersonBorrower(personBorrower))
})

export default connect(
    mapStateToProps,
    mapDispatchToProps
)(PersonTable)