import React from "react";
import PropTypes from 'prop-types'

const ClientCheckComponent = ({ debt, person, updateClient }) => (
    <div>
      <input type="checkbox"
             id={'clientDebt'+debt.id+'person'+person.id}
             defaultChecked={debt.clients.indexOf(person.id) >= 0}
             onChange={ event => {
               updateClient({
                 debtId: debt.id,
                 personId: person.id,
                 isClient: event.target.checked
               });
             }}/>
      <label className="pl-1" htmlFor={'clientDebt'+debt.id+'person'+person.id}>{person.name}</label>
    </div>
)

ClientCheckComponent.propTypes = {
  debt: PropTypes.shape({
    id: PropTypes.number.isRequired,
    clients: PropTypes.arrayOf(PropTypes.number.isRequired)
  }).isRequired,
  person: PropTypes.shape({
    id: PropTypes.number.isRequired,
    name: PropTypes.string.isRequired,
  }).isRequired,
  updateClient: PropTypes.func.isRequired,
}

export default ClientCheckComponent