import {PersonEvents} from "./person.constants";

const personReducer = (state = [], action) => {
  switch (action.type) {

    case PersonEvents.ADD_PERSON:
      return [...state, action.payload]

    case PersonEvents.DELETE_PERSON:
      return [...state.filter(person => person.id !== action.id)];

    case PersonEvents.UPDATE_PERSON_BORROWER:
      return state.map(
          person =>
              person.id === action.personBorrower.id
                  ? Object.assign({}, person, {isBorrower: action.personBorrower.isBorrower})
                  : person
      )

    default:
      return state
  }
}

export default personReducer