import React from 'react'
import PropTypes from 'prop-types'

const ConsolidatedCheckComponent = ({ id, isConsolidated, updateIsConsolidated }) => (
  <div>
    <input type="checkbox"
         id={'isConsolidated'+id}
         defaultChecked={isConsolidated}
         onChange={ event => {
           updateIsConsolidated({
               id: id,
               isConsolidated: event.target.checked
           });
         }}/>
  </div>
)

ConsolidatedCheckComponent.propTypes = {
  id: PropTypes.number.isRequired,
  isConsolidated: PropTypes.bool.isRequired,
  updateIsConsolidated: PropTypes.func.isRequired,
}

export default ConsolidatedCheckComponent