import React from 'react'
import PropTypes from 'prop-types'
import DebtTableRowComponent from "./debt-table-row.component";

const DebtTableComponent = ({ debtList, personList, deleteDebt, updateIsConsolidated, updateIsNewDebt, updateClient }) => (
    <table className="table table-hover">
      <thead>
        <tr>
          <th className="text-left">Type</th>
          <th className="text-right">Lendable Equity</th>
          <th className="text-right">Total Value</th>
          <th className="text-right">Amount Owed</th>
          <th className="text-right">Monthly Payment</th>
          <th className="text-right">Remaining Term <small>(Months)</small></th>
          <th className="text-right">Interest Rate</th>
          <th>Consolidated</th>
          <th>New Debt</th>
          <th>Clients</th>
          <th className="text-center">Action</th>
        </tr>
      </thead>
      <tbody>
        {debtList.map(debt => (
            <DebtTableRowComponent key={debt.id}
                                   debt={debt}
                                   personList={personList}
                                   onDelete={() => deleteDebt(debt.id)}
                                   updateIsConsolidated={updateIsConsolidated}
                                   updateIsNewDebt={updateIsNewDebt}
                                   updateClient={updateClient}/>
        ))}

      </tbody>
    </table>
)

DebtTableComponent.propTypes = {
  debtList: PropTypes.arrayOf(
      PropTypes.shape({
        id: PropTypes.number.isRequired,
        debtType: PropTypes.string.isRequired,
        totalValue:  PropTypes.number.isRequired,
        amountOwed:  PropTypes.number.isRequired,
        interestRate:  PropTypes.number.isRequired,
        monthlyPayment:  PropTypes.number.isRequired,
        remainingTermMonths:  PropTypes.number.isRequired,
        isConsolidated: PropTypes.bool.isRequired,
        isNewDebt: PropTypes.bool.isRequired,
        clients: PropTypes.arrayOf(PropTypes.number.isRequired)
      }).isRequired
  ).isRequired,
  deleteDebt: PropTypes.func.isRequired,
  updateIsConsolidated: PropTypes.func.isRequired,
  updateIsNewDebt: PropTypes.func.isRequired,
  updateClient: PropTypes.func.isRequired,
}

export default DebtTableComponent
