import {DebtEvents} from "./debt.constants";

let nextDebt = 0
export const addDebt = (debt) => {
  debt.id = nextDebt++
  return {
    type: DebtEvents.ADD_DEBT,
    debt
  }
}

export const deleteDebt = id => ({
  type: DebtEvents.DELETE_DEBT,
  id
})

export const updateIsConcolidated = debtConsolidated => ({
  type: DebtEvents.UPDATE_IS_CONSOLIDATED,
  debtConsolidated
})

export const updateIsNewDebt = debtIsNew => ({
  type: DebtEvents.UPDATE_IS_NEW_DEBT,
  debtIsNew
})

export const updateClient = debtClient => ({
  type: DebtEvents.UPDATE_CLIENT,
  debtClient
})