import connect from "react-redux/es/connect/connect";
import DebtTableComponent from "./debt-table.component";
import {deleteDebt, updateClient, updateIsConcolidated, updateIsNewDebt} from "../../redux/debt/debt.actions";

const mapStateToProps = state => ({
  debtList: state.debtList,
  personList: state.personList
})

const mapDispatchToProps = dispatch => ({
  deleteDebt: id => dispatch(deleteDebt(id)),
  updateIsConsolidated: debtConsolidated => dispatch(updateIsConcolidated(debtConsolidated)),
  updateIsNewDebt: debtIsNew => dispatch(updateIsNewDebt(debtIsNew)),
  updateClient: debtClient => dispatch(updateClient(debtClient)),
})

export default connect(
    mapStateToProps,
    mapDispatchToProps
)(DebtTableComponent)