import React from 'react'
import PropTypes from 'prop-types'

const NewDebtCheckComponent = ({ id, isNewDebt, updateIsNewDebt }) => (
  <div>
    <input type="checkbox"
         id={'isNewDebt'+id}
         defaultChecked={isNewDebt}
         onChange={ event => {
           updateIsNewDebt({
               id: id,
               isNewDebt: event.target.checked
           });
         }}/>
  </div>
)

NewDebtCheckComponent.propTypes = {
  id: PropTypes.number.isRequired,
  isNewDebt: PropTypes.bool.isRequired,
  updateIsNewDebt: PropTypes.func.isRequired,
}

export default NewDebtCheckComponent