import {PersonEvents} from "./person.constants";

let nextPersonId = 0
export const addPerson = person => {
  person.id = nextPersonId++
  return {
    type: PersonEvents.ADD_PERSON,
    payload: person
  }
}

export const deletePerson = id => ({
  type: PersonEvents.DELETE_PERSON,
  id
})

export const updatePersonBorrower = personBorrower => ({
  type: PersonEvents.UPDATE_PERSON_BORROWER,
  personBorrower
})