import {DebtEvents} from "./debt.constants";
import debtClientReducer from "./debt-client.reducer";
import {PersonEvents} from "../person/person.constants";

const debtReducer = (state = [], action) => {
  switch (action.type) {

    case DebtEvents.ADD_DEBT:
      return [...state, action.debt]

    case DebtEvents.DELETE_DEBT:
      return [...state.filter(debt => debt.id !== action.id)]

    case DebtEvents.UPDATE_IS_CONSOLIDATED:
      return state.map(
          debt =>
              debt.id === action.debtConsolidated.id
                  ? Object.assign({}, debt, {isConsolidated: action.debtConsolidated.isConsolidated})
                  : debt
      )

    case DebtEvents.UPDATE_IS_NEW_DEBT:
      return state.map(
          debt =>
              debt.id === action.debtIsNew.id
                  ? Object.assign({}, debt, {isNewDebt: action.debtIsNew.isNewDebt})
                  : debt
      )

    case DebtEvents.UPDATE_CLIENT:
      return state.map(
          debt =>
              debt.id === action.debtClient.debtId
                  ? Object.assign({}, debt, {clients: debtClientReducer(debt.clients, action)})
                  : debt
      )

    case PersonEvents.DELETE_PERSON:
      return state.map(
          debt => Object.assign({}, debt, {clients: debtClientReducer(debt.clients, action)})
      )

    default:
      return state
  }
}

export default debtReducer