import React from "react";
import PropTypes from 'prop-types'
import {DebtTypes, getDefaultLendableEquity, validateDebt} from "../../redux/debt/debt.constants";

let formRef, debtType, lendableEquity, totalValue, amountOwed, monthlyPayment, remainingTermMonths, interestRate, isConsolidated, isNewDebt, clients;
clients = [];
lendableEquity = {};
debtType = {value: DebtTypes.AUTO};

const submit = (e, addDebt) => {
  e.preventDefault()

  const debt = {
    debtType: debtType.value,
    lendableEquity: +lendableEquity.value.trim(),
    totalValue: +totalValue.value.trim(),
    amountOwed: +amountOwed.value.trim(),
    monthlyPayment: +monthlyPayment.value.trim(),
    remainingTermMonths: +remainingTermMonths.value.trim(),
    interestRate: +interestRate.value.trim(),
    isConsolidated: isConsolidated.checked,
    isNewDebt: isNewDebt.checked,
    clients: clients.filter(c => c.node.checked).map(c => c.id)
  }

  if (validateDebt(debt)) {
    addDebt(debt)
  } else {
    return
  }

  formRef.reset();
  debtType.value = DebtTypes.AUTO;
  debtType.focus();
}

const DebtFormComponent = ({ personList, addDebt }) => (
    <form ref={(el) => (formRef = el)}
          onSubmit={e => submit(e, addDebt)}>
      <div className="row">
        <div className="col">
          <label htmlFor="type">Type</label>
          <select id="type"
                  className="form-control"
                  ref={node => (debtType = node)}
                  onChange={event => {lendableEquity.value = getDefaultLendableEquity(debtType.value)}}
          >
            {Object.keys(DebtTypes).map(debtType =>
              <option key={DebtTypes[debtType]} value={DebtTypes[debtType]}>{DebtTypes[debtType]}</option>
            )}
          </select>
        </div>
        <div className="col">
          <label htmlFor="lendableEquity"
                 className="text-nowrap" >Lendable Equity</label>
          <input id="lendableEquity"
                 defaultValue={getDefaultLendableEquity(debtType.value)}
                 type="number"
                 className="form-control"
                 min={0}
                 step={0.01}
                 ref={node => (lendableEquity = node)}
                 required />
        </div>
        <div className="col">
          <label htmlFor="totalValue"
                 className="text-nowrap" >Total Value</label>
          <input id="totalValue"
                 type="number"
                 className="form-control"
                 min={0}
                 step={0.01}
                 ref={node => (totalValue = node)}
                 required />
        </div>
        <div className="col">
          <label htmlFor="amountOwed"
                 className="text-nowrap" >Amount Owed</label>
          <input id="amountOwed"
                 type="number"
                 className="form-control"
                 min={0}
                 step={0.01}
                 ref={node => (amountOwed = node)}
                 required />
        </div>
        <div className="col">
          <label htmlFor="monthlyPayment"
                 className="text-nowrap">Monthly Payment</label>
          <input id="monthlyPayment"
                 type="number"
                 className="form-control"
                 min={0}
                 step={0.01}
                 ref={node => (monthlyPayment = node)}
                 required />
        </div>
        <div className="col">
          <label htmlFor="remainingTermMonths"
                 className="text-nowrap">Remaining Term <small>(Months)</small></label>
          <input id="remainingTermMonths"
                 type="number"
                 className="form-control"
                 min={0}
                 step={0.01}
                 ref={node => (remainingTermMonths = node)}
                 required />
        </div>
        <div className="col">
          <label htmlFor="interestRate"
                 className="text-nowrap">Interest Rate</label>
          <input id="interestRate"
                 type="number"
                 className="form-control"
                 min={0}
                 step={0.01}
                 ref={node => (interestRate = node)}
                 required />
        </div>
      </div>

      <div className="row">
        <div className="col-auto">
          <input id="isConsolidated"
                 defaultChecked={false}
                 type='checkbox'
                 ref={node => (isConsolidated = node)}/>
          <label htmlFor="isConsolidated" className="pl-2">Consolidated</label>
        </div>
        <div className="col-auto">
          <input id="isNewDebt"
                 defaultChecked={false}
                 type='checkbox'
                 ref={node => (isNewDebt = node)}/>
          <label htmlFor="isNewDebt" className="pl-2">New Debt</label>
        </div>
      </div>

      <fieldset>
        <legend>Clients</legend>
        <div className="row">
          {personList.map(person =>
              <div key={person.id}
                   className="col-auto">
                <input id={'isClient'+person.id}
                       defaultChecked={false}
                       type='checkbox'
                       ref={node => (clients[person.id] = {node: node, id: person.id})}/>
                <label htmlFor={'isClient'+person.id} className="pl-2">{person.name}</label>
              </div>
          )}
        </div>
      </fieldset>
      <button className="btn btn-primary" type="submit">Add Debt</button>
    </form>
)

DebtFormComponent.propTypes = {
  addDebt: PropTypes.func.isRequired
}

export default DebtFormComponent