import React from 'react'
import PropTypes from 'prop-types'
import NumberFormat from 'react-number-format';
import BorrowerCheckComponent from "./borrower-check.component";

const PersonTableRowComponent = ({ person, onDelete, updatePersonBorrower }) => (
    <tr>
      <td className="text-left">{person.name}</td>
      <td className="text-right"><NumberFormat value={person.annualIncome} displayType={'text'} thousandSeparator={true} prefix={'$'} /></td>
      <td className="text-center">
        <BorrowerCheckComponent isBorrower={person.isBorrower}
                                id={person.id}
                                updatePersonBorrower={updatePersonBorrower}/>
      </td>
      <td className="text-center">
        <button className="btn btn-sm btn-danger"
                onClick={onDelete}>
          Delete
        </button>
      </td>
    </tr>
)

PersonTableRowComponent.propTypes = {
  person: PropTypes.shape({
    id: PropTypes.number.isRequired,
    name: PropTypes.string.isRequired,
    annualIncome:  PropTypes.number.isRequired,
    isBorrower: PropTypes.bool.isRequired,
  }).isRequired,
  onDelete: PropTypes.func.isRequired,
  updatePersonBorrower: PropTypes.func.isRequired,
}

export default PersonTableRowComponent