import React from 'react'
import PropTypes from 'prop-types'
import PersonTableRowComponent from './person-table-row.component';

const PersonTable = ({ personList, deletePerson, updatePersonBorrower }) => (
    <table className="table table-hover">
      <thead>
        <tr>
          <th className="text-left">Name</th>
          <th className="text-right">Annual Income</th>
          <th className="text-center">Is Borrower</th>
          <th className="text-center">Remove</th>
        </tr>
      </thead>
      <tbody>
        {personList.map(person => (
            <PersonTableRowComponent key={person.id}
                                     person={person}
                                     onDelete={() => deletePerson(person.id)}
                                     updatePersonBorrower={updatePersonBorrower}
            />
        ))}
      </tbody>
    </table>
)

PersonTable.propTypes = {
  personList: PropTypes.arrayOf(
      PropTypes.shape({
        id: PropTypes.number.isRequired,
        name: PropTypes.string.isRequired,
        annualIncome:  PropTypes.number.isRequired,
        isBorrower: PropTypes.bool.isRequired,
      }).isRequired
  ).isRequired,
  deletePerson: PropTypes.func.isRequired,
  updatePersonBorrower: PropTypes.func.isRequired,
}

export default PersonTable