import React from 'react'
import PropTypes from 'prop-types'

let name, annualIncome, isBorrower, formRef;

const PersonFormComponent = ({ onAddPerson }) => (
  <div>
    <form ref={(el) => (formRef = el)}
          onSubmit={e => {
            e.preventDefault();

            if (!name.value.trim() || !+annualIncome.value.trim()) {
              return
            }

            onAddPerson({
              name: name.value,
              annualIncome: +annualIncome.value,
              isBorrower: isBorrower.checked
            });

            name.value = '';
            formRef.reset();
            name.focus();
          }}
    >
      <div className="row">
        <div className="col">
          <label htmlFor="name">Name</label>
          <input id="name"
                 className="form-control"
                 autoComplete='off'
                 required={true}
                 ref={node => (name = node)} />
        </div>
        <div className="col">
          <label htmlFor="annualIncome">Annual Income</label>
          <input id="annualIncome"
                 className="form-control"
                 autoComplete='off'
                 required={true}
                 min={1}
                 ref={node => (annualIncome = node)}
                 type='number' />
        </div>
      </div>
      <div className="row">
        <div className="col">
          <input id="isBorrower"
                 ref={node => (isBorrower = node)}
                 defaultChecked={true}
                 type='checkbox'/>
          <label htmlFor="isBorrower" className="pl-2">Is Borrower</label>
        </div>
      </div>
      <button className="btn btn-primary" type="submit">Add Person</button>
    </form>
  </div>
)

PersonFormComponent.propTypes = {
  onAddPerson: PropTypes.func.isRequired
}

export default PersonFormComponent