export const DebtEvents = {
  ADD_DEBT: 'ADD_DEBT',
  DELETE_DEBT: 'DELETE_DEBT',
  UPDATE_IS_CONSOLIDATED: 'UPDATE_IS_CONSOLIDATED',
  UPDATE_IS_NEW_DEBT: 'UPDATE_IS_NEW_DEBT',
  UPDATE_CLIENT: 'UPDATE_CLIENT'
}

export const DebtTypes = {
  AUTO: 'Auto',
  HOME: 'Home',
  INVESTMENT_PROPERTY: 'Investment Property',
  INVESTMENT: 'Investment',
  UNSECURED: 'Unsecured',
  OTHER: 'Other',
}

export const getDefaultLendableEquity = debtType => {
  switch (debtType) {
    case DebtTypes.AUTO:
      return 125
    case DebtTypes.HOME:
    case DebtTypes.OTHER:
      return 100
    case DebtTypes.INVESTMENT_PROPERTY:
      return 75
    case DebtTypes.INVESTMENT:
      return 50
    default:
      return 0
  }
}

// debt validation functions, maybe put elsewhere? or also call it in the create debt action maybe?

export const validateDebt = debt => {
  if(
      validateDebtType(debt.debtType)
      && validateNumberGTEZero(debt.lendableEquity)
      && validateNumberGTEZero(debt.totalValue)
      && validateNumberGTEZero(debt.amountOwed)
      && validateNumberGTEZero(debt.monthlyPayment)
      && validateNumberGTEZero(debt.remainingTermMonths)
      && validateNumberGTEZero(debt.interestRate)
  ) {
    return true
  } else {
    return false
  }
}

const validateNumberGTEZero = number => +number >= 0

const validateDebtType = debtType => {
  switch (debtType) {
    case DebtTypes.HOME:
    case DebtTypes.AUTO:
    case DebtTypes.INVESTMENT:
    case DebtTypes.INVESTMENT_PROPERTY:
    case DebtTypes.UNSECURED:
    case DebtTypes.OTHER:
      return true
    default:
      return false
  }
}