import React from 'react'
import PropTypes from 'prop-types'

const BorrowerCheckComponent = ({ id, isBorrower, updatePersonBorrower }) => (
  <div>
    <input type="checkbox"
         id={'isBorrowerCheck'+id}
         defaultChecked={isBorrower}
         onChange={ event => {
           updatePersonBorrower({
               id: id,
               isBorrower: event.target.checked
           });
         }}/>
    {/*<label for={'isBorrowerCheck'+id}>{String(isBorrower)}</label>*/}
  </div>
)

BorrowerCheckComponent.propTypes = {
  id: PropTypes.number.isRequired,
  isBorrower: PropTypes.bool.isRequired,
  updatePersonBorrower: PropTypes.func.isRequired,
}

export default BorrowerCheckComponent