import React from 'react'
import AddPerson from "./person/person-form.container";
import PersonList from "./person/person-table.container";
import AddDebt from "./debt/debt-form.container";
import DebtTable from "./debt/debt-table.container";
import Results from "./results/results.container"

const getDisabled = (personList, debtList) => {
  return personList.length > 0 && debtList.length > 0
    ? ""
    : "disabled"
}


const TabsComponent = ({ personList, debtList }) => (
    <div>
      <nav>
        <div className="nav nav-tabs" id="nav-tab" role="tablist">
          <a className="nav-item nav-link active" id="nav-person-tab" data-toggle="tab" href="#nav-person-list" role="tab" aria-controls="nav-person-list" aria-selected="true">Clients</a>
          <a className="nav-item nav-link" id="nav-debt-tab" data-toggle="tab" href="#nav-debt-list" role="tab" aria-controls="nav-debt-list" aria-selected="false">Debts</a>
          <a className={"nav-item nav-link "+getDisabled(personList, debtList)} id="nav-result-tab" data-toggle="tab" href="#nav-result-list" role="tab" aria-controls="nav-result-list" aria-selected="false">Results</a>
        </div>
      </nav>
      <div className="tab-content" id="nav-tabContent">
        <div className="tab-pane fade show active" id="nav-person-list" role="tabpanel" aria-labelledby="nav-home-tab">
          <h2>Clients</h2>
          <AddPerson />
          <div className="mt-3">
            <PersonList />
          </div>
        </div>
        <div className="tab-pane fade" id="nav-debt-list" role="tabpanel" aria-labelledby="nav-profile-tab">
          <h2>Debts</h2>
          <AddDebt />
          <div className="mt-3">
            <DebtTable />
          </div>
        </div>
        <div className="tab-pane fade" id="nav-result-list" role="tabpanel" aria-labelledby="nav-contact-tab">
          <h2>Results</h2>
          <div className="mt-3">
            <Results />
          </div>
        </div>
      </div>
    </div>
)

export default TabsComponent